	const vh = (coef) => window.innerHeight * (coef/100);
	const vw = (coef) => window.innerWidth * (coef/100);
	
	export default {
		data() {
			return {
				title: "SenzÂ®"
			}
		},

		head() {
			return {
				title: this.title,
				meta: [{
					hid: "description",
					name: "description",
					content: "Whether you're a startup or a growing company, we provide everything you need to reach your clients."
				}]
			}
		},

		mounted() {
			this.animateOnScroll()
			this.bgColor()
			this.projectImagesScroll01()
			this.projectImagesScroll02()
		},

		methods: {
			animateOnScroll() {
      			this.$gsap.to('.projects .project-image', {
        			scrollTrigger: {
          				trigger: '.projects .project-image',
						start: vh(-5) + 'top',
    					end: vh(250) + ' top',
          				pin: true,
          				scrub: true,
        			}
      			})
    		},

			bgColor() {
				this.$gsap.to('body', {
					backgroundColor: '#fff',
					scrollTrigger: {
          				trigger: '.projects-title',
						start: vh(-10) + 'top',
    					end: vh(20) + 'bottom',
          				scrub: true,
        			}
				})
			},

			projectImagesScroll01() {
				this.$gsap.to('.project-link-01', {
					autoAlpha: 0,
					scrollTrigger: {
          				trigger: '.description',
						start: vh(70) + 'top',
    					end: vh(90) + ' top',
          				scrub: true,
        			}
				})
			},

			projectImagesScroll02() {
				this.$gsap.to('.project-link-02', {
					autoAlpha: 0,
					scrollTrigger: {
          				trigger: '.description',
						start: vh(170) + 'top',
    					end: vh(190) + ' top',
          				scrub: true,
						//markers: {startColor: "blue", endColor: "purple", fontSize: "12px"}
        			}
				})
			},
		},
	}
